import config from '../gatsby-config'

const { pathPrefix } = config
const redirects = new Map()
const oldPages = [{ old: 'all-options', new: 'all-props' }]

oldPages.forEach(page => {
  redirects.set(`${pathPrefix}/${page.old}/`, `${pathPrefix}/${page.new}/`)
  redirects.set(`/${page.old}/`, `/${page.new}/`)
})

export default redirects
